.station {
  display: flex;
  align-items: flex-start;
  max-width: 900px;
  &__var {
    width: 100%; }
  &__const {
    width: 35%;
    min-width: 35%;
    margin-right: 10px; }
  &__title {
    display: inline-block;
    margin-bottom: 10px; }
  &__prop {
    margin-bottom: 5px;
    font-size: 16px;
    opacity: .7;
    line-height: 1.2;
    @include overflow-text(); }
  &__img {
    display: inline-block;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: top center; }
  &__link {
    margin-top: 10px;
    font-size: 16px;
    a {
      @include line-hover; } } }

@media only screen and (min-width: 600px) {
  .station {
    &__const {
      min-width: 200px;
      max-width: 200px;
      margin-right: 40px; } } }

.is-night {
  .station__link a {
    @include line-hover-night-override; } }
