.search {
  width: 100%;
  &__form {
    display: flex; }
  &__field {
    max-width: 500px;
    width: 100%;
    @include line-focus; }
  input {
    padding: 5px 0;
    width: 100%; }
  &__btn {
    padding: 5px;
    font-size: 18px;
    font-weight: bold;
    opacity: .5;
    transition: $transition;
    &:hover {
      color: $color-accent; } } }

.is-night {
  .search__field {
    @include line-focus-night-override; } }
